<template>
  <section id="skills">
    <v-row>
      <v-col cols="12" xs="12" sm="12" md="12" lg="6" class="p-2 text-box-pd">
        <div class="text-box">
          <span class="vertical-text">{{ $t("ui.sections.skills.title") }}</span>
          <div class="horizontal-text">
            <h2>{{ $t("ui.sections.skills.text-box.title") }}</h2>
            <p class="justified-text">
              {{ $t("ui.sections.skills.text-box.text") }}
            </p>
          </div>
        </div>
      </v-col>
      <v-col cols="12" xs="12" sm="12" md="12" lg="6" class="p-2">
        <SkillsComponent/>
      </v-col>
    </v-row>
  </section>
</template>

<script>
import SkillsComponent from '@/components/SkillsComponent'
export default {
  name: "skills-section",
  
  components: {
    SkillsComponent
  },

  data: function() {
    return {
      valid: true,
      name: "",
      nameRules: [
        (v) => !!v || `${this.$t("ui.sections.contact.form.error.name-required")}`,
        (v) => (v && v.length <= 20) || `${this.$t("ui.sections.contact.form.error.name-length")}`,
      ],
      email: "",
      emailRules: [
        (v) => !!v || `${this.$t("ui.sections.contact.form.error.email-required")}`,
        (v) => /.+@.+\..+/.test(v) || `${this.$t("ui.sections.contact.form.error.email-valid")}`,
      ],
      messageRules: [
        (v) => !!v || `${this.$t("ui.sections.contact.form.error.message-required")}`,
      ],
      select: null,
      bio: ""
    }
  },

  methods: {
    submit() {
      this.$refs.form.validate();
    },
    reset() {
      this.$refs.form.reset();
    },
  },
};
</script>

<style scoped>
section {
  box-sizing: border-box;
  padding-bottom: 30px;
  padding: 2vh;
}

.text-box-pd {
  padding-top: 40px !important;
}

.form-style {
  width: 100%;
  padding: 2vh;
}

@media (min-width: 992px) {
.horizontal-text {
  padding-left: 40px;
}
}

.text-box {
  position: relative;
}

.justified-text {
  text-align: justify;
}

.vertical-text {
  text-transform: uppercase;
}

@media (min-width: 992px) {
  .vertical-text {
    position: absolute;
    top: 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }
  .vertical-text {
    left: 0;
  }

  /*
  RIGHT POSSITIONING

  .vertical-text {
    right: 0;
  }
  
  */

  .vertical-text {
    -webkit-transform: rotate(270deg) translateX(-100%);
    transform: rotate(270deg) translateX(-100%);
  }

  /*
  RIGHT POSSITIONING

  .vertical-text {
    -webkit-transform: rotate(-270deg) translateX(100%);
            transform: rotate(-270deg) translateX(100%);
  }
  
  */

  .vertical-text {
    -webkit-transform-origin: left top;
    transform-origin: left top;
  }

  /*
  RIGHT POSSITIONING
  
  .vertical-text {
    -webkit-transform-origin: right top;
            transform-origin: right top;
  }
  
  */

  .vertical-text:before {
    content: "";
    display: inline-block;
    height: 2px;
    width: 50px;
  }
  .vertical-text:before {
    margin-right: 10px;
  }

  /*
  RIGHT POSSITIONING

  .vertical-text:before {
    margin-left: 20px;
  }
  
  */

  .vertical-text:before {
    background-color: #9f9f9f; /* #5f5f5f O #9f9f9f */
  }
}

@media (max-width: 991px) {
  .vertical-text:after {
    content: "";
    display: block;
    height: 2px;
    width: 60px;
    margin-top: 5px;
    margin-bottom: 16px;
  }
  .vertical-text:after {
    background-color: #9f9f9f;
  }
}
</style>
