<template>
  <section id="contact">
    <v-row>
      <v-col cols="12" xs="12" sm="12" md="12" lg="6" class="p-2">
        <div class="text-box">
          <span class="vertical-text">{{ $t("ui.sections.contact.title") }}</span>
          <div class="horizontal-text">
            <h2>{{ $t("ui.sections.contact.text-box.title-1") }} <br> {{ $t("ui.sections.contact.text-box.title-2") }}</h2>
            <p class="justified-text text-r-pad">
              {{ $t("ui.sections.contact.text-box.text") }}
            </p>
          </div>
        </div>
        <ul class="contact-info">
          <li>
            <v-icon class="contact-info-icon ">mdi-phone</v-icon>
            <div>
              <strong class="contact-info-title">{{ $t("ui.sections.contact.phone") }}:</strong>
              <ul><li>{{ $t("phone") }}</li></ul>
            </div>
          </li>
          <li>
            <v-icon class="contact-info-icon ">mdi-email</v-icon>
            <div>
              <strong class="contact-info-title">{{ $t("ui.sections.contact.email") }}:</strong>
              <ul><li>{{ $t("email") }}</li></ul>
            </div>
          </li>
        </ul>
      </v-col>
      <v-col cols="12" xs="12" sm="12" md="12" lg="6" class="p-2">
        <v-card elevation="6" shaped>
          <v-card-actions>
            <v-form
              class="form-style"
              ref="form"
              v-model="valid"
              lazy-validation
            >
              <v-text-field
                v-model="name"
                :counter="10"
                :rules="nameRules"
                :label="$t('ui.sections.contact.form.input-name')"
                required
              ></v-text-field>

              <v-text-field
                v-model="email"
                :rules="emailRules"
                :label="$t('ui.sections.contact.form.input-email')"
                required
              ></v-text-field>

              <v-textarea :rules="messageRules" v-model="bio" color="teal">
                <template v-slot:label>
                  <div>{{ $t('ui.sections.contact.form.input-message') }}</div>
                </template>
              </v-textarea>

              <v-btn :disabled="!valid" class="mr-4" @click="snackbar = true">
                {{ $t('ui.sections.contact.form.btn-submit') }}
              </v-btn>

              <v-btn class="mr-4" @click="reset">{{ $t('ui.sections.contact.form.btn-clear') }}</v-btn>
            </v-form>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <v-snackbar
      v-model="snackbar"
    >
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color="pink"
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          {{ $t("ui.snackbar.close") }}
        </v-btn>
      </template>
    </v-snackbar>
  </section>
</template>

<script>
import i18n from '../i18n'

export default {
  name: "contact-section",

  data: function() {
    return {
      valid: true,
      name: "",
      nameRules: [
        (v) => !!v || `${this.$t("ui.sections.contact.form.error.name-required")}`,
        (v) => (v && v.length <= 20) || `${this.$t("ui.sections.contact.form.error.name-length")}`,
      ],
      email: "",
      emailRules: [
        (v) => !!v || `${this.$t("ui.sections.contact.form.error.email-required")}`,
        (v) => /.+@.+\..+/.test(v) || `${this.$t("ui.sections.contact.form.error.email-valid")}`,
      ],
      messageRules: [
        (v) => !!v || `${this.$t("ui.sections.contact.form.error.message-required")}`,
      ],
      select: null,
      bio: "",
      snackbar: false,
      text: `${this.$t("ui.snackbar.text")}`,
      locale: i18n.locale
    }
  },

  methods: {
    submit() {
      this.$refs.form.validate();
      
    },
    reset() {
      this.$refs.form.reset();
    },
  },
  watch: {
    locale: function(val){
      console.log("changed: " + val);
    }
  }
};
</script>

<style scoped>
section {
  box-sizing: border-box;
  padding: 2vh;
  padding-bottom: 50px;
}

.contact-info {
  padding-top: 50px;
}

.contact-info-title {
  margin-bottom: 5px;
}

.form-style {
  width: 100%;
  padding: 2vh;
}

@media (min-width: 992px) {
  .horizontal-text {
    padding-left: 40px;
  }
}

ul, ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  margin-bottom: 20px;
  display: flex;
  align-items: flex-start;
}

.contact-info-icon {
  margin-right: 18px;
}

.text-box {
  position: relative;
}

.justified-text {
  text-align: justify;
}

.vertical-text {
  text-transform: uppercase;
}

@media (min-width: 992px) {
  .vertical-text {
    position: absolute;
    top: 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }
  .vertical-text {
    left: 0;
  }

  .text-r-pad {
    padding-right: 10%;
  }

  /*
  RIGHT POSSITIONING

  .vertical-text {
    right: 0;
  }
  
  */

  .vertical-text {
    -webkit-transform: rotate(270deg) translateX(-100%);
    transform: rotate(270deg) translateX(-100%);
  }

  /*
  RIGHT POSSITIONING

  .vertical-text {
    -webkit-transform: rotate(-270deg) translateX(100%);
            transform: rotate(-270deg) translateX(100%);
  }
  
  */

  .vertical-text {
    -webkit-transform-origin: left top;
    transform-origin: left top;
  }

  /*
  RIGHT POSSITIONING
  
  .vertical-text {
    -webkit-transform-origin: right top;
            transform-origin: right top;
  }
  
  */

  .vertical-text:before {
    content: "";
    display: inline-block;
    height: 2px;
    width: 50px;
  }
  .vertical-text:before {
    margin-right: 10px;
  }

  /*
  RIGHT POSSITIONING

  .vertical-text:before {
    margin-left: 20px;
  }
  
  */

  .vertical-text:before {
    background-color: #9f9f9f; /* #5f5f5f O #9f9f9f */
  }
}

@media (max-width: 991px) {
  .vertical-text:after {
    content: "";
    display: block;
    height: 2px;
    width: 60px;
    margin-top: 5px;
    margin-bottom: 16px;
  }
  .vertical-text:after {
    background-color: #9f9f9f;
  }
}
</style>
