<template>
  <v-menu>
    <template v-slot:activator="{ on, attrs }">
      <v-btn class="text-capitalize" v-bind="attrs" v-on="on" text>
        <v-icon left>mdi-translate</v-icon>
        {{ activeLang }}
        <v-icon small right>mdi-menu-down</v-icon>
      </v-btn>
    </template>
    <v-list dense>
      <v-list-item
        v-for="(lang, index) in langs"
        :key="index"
        @click="handleLanguageChange(lang)"
      >
        <v-list-item-title>{{ lang.title }}</v-list-item-title>
      </v-list-item>
    </v-list>
  </v-menu>
</template>

<script>
import langsList from "@/config/lang/langs";

export default {
  name: "locale-changer",
  data() {
    return {
      langs: Object.values(langsList),
      activeLang: langsList[0].title,
    };
  },
  methods: {
    handleLanguageChange(lang) {
      if(this.$root.$i18n.locale !== lang.value){
        this.$root.$i18n.locale = lang.value;
        this.activeLang = lang.title;
      }
    },
  },
};
</script>

<style scoped>
.v-size--default {
   height: auto !important;
}
</style>