}

.text-box {
  position: relative;
}

.justified-text {
  text-align: justify;
}

.vertical-text {
  text-transform: uppercase;
}

@media (min-width: 992px) {
  .vertical-text {
    position: absolute;
    top: 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }
  .vertical-text {
    left: 0;
  }

  /*
  RIGHT POSSITIONING

  .vertical-text {
    right: 0;
  }
  
  */

  .vertical-text {
    -webkit-transform: rotate(270deg) translateX(-100%);
    transform: rotate(270deg) translateX(-100%);
  }

  /*
  RIGHT POSSITIONING

  .vertical-text {
    -webkit-transform: rotate(-270deg) translateX(100%);
            transform: rotate(-270deg) translateX(100%);
  }
  
  */

  .vertical-text {
    -webkit-transform-origin: left top;
    transform-origin: left top;
  }

  /*
  RIGHT POSSITIONING
  
  .vertical-text {
    -webkit-transform-origin: right top;
            transform-origin: right top;
  }
  
  */

  .vertical-text:before {
    content: "";
    display: inline-block;
    height: 2px;
    width: 50px;
  }
  .vertical-text:before {
    margin-right: 10px;
  }

  /*
  RIGHT POSSITIONING

  .vertical-text:before {
    margin-left: 20px;
  }
  
  */

  .vertical-text:before {
    background-color: #9f9f9f; /* #5f5f5f O #9f9f9f */
  }
}

@media (max-width: 991px) {
  .vertical-text:after {
