          </div>
        </div>
      </v-col>
    </v-row>
    <!--<v-snackbar
      v-model="snackbar"
    >
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color="pink"
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          {{ $t("ui.snackbar.close") }}
        </v-btn>
      </template>
    </v-snackbar>-->
