<template>
  <b-navbar class="py-0" id="myNavBar" fixed="top" toggleable="lg" :type="activeTheme">
    <v-container>
      <b-navbar-brand class="title-text" href="index.html"
        >{{$t("name")}}</b-navbar-brand
      >

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav :class="{'text-white !important': isDark,'text-black !important': !isDark}" class="caption text-uppercase ml-auto ml-6 menu">
          <b-nav-item class="nav-item-padding-first" href="#hero">{{
            $t("ui.sections.hero.title")
          }}</b-nav-item>
          <b-nav-item class="nav-item-padding"  href="#about">{{
            $t("ui.sections.about.title")
          }}</b-nav-item>
          <b-nav-item class="nav-item-padding"  href="#skills">{{
            $t("ui.sections.skills.title")
          }}</b-nav-item>
          <b-nav-item class="nav-item-padding"  href="#portfolio">{{
            $t("ui.sections.portfolio.title")
          }}</b-nav-item>
          <b-nav-item class="nav-item-padding"  href="#contact">{{
            $t("ui.sections.contact.title")
          }}</b-nav-item>
        </b-navbar-nav>

        <b-navbar-nav class="ml-auto d-none d-md-flex">
          <locale-changer></locale-changer>
          <v-switch class="pl-2" v-model="isDark" @click="darkMode"></v-switch>
          <v-icon class="pl-2">mdi-weather-night</v-icon>
        </b-navbar-nav>
      </b-collapse>
    </v-container>
  </b-navbar>
</template>

<script>
import LocaleChanger from "@/components/LocaleChanger.vue";

export default {
  components: {
   LocaleChanger,
  },

  data() {
    return {
      isDark: this.$vuetify.theme.dark,
      activeTheme: this.$vuetify.theme.dark === true ? "dark" : "light",
      locale: this.$root.$i18n.locale
    };
  },

  methods: {
    darkMode() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
      this.activeTheme = this.$vuetify.theme.dark === true ? "dark" : "light";
    },
    handleScroll () {
      var theme = this.isDark ? 'bg-dark' : 'bg-light';
      var nav = document.getElementById("myNavBar");
      if(window.scrollY <= 150){
        nav.classList.remove(theme);
      }else if(window.scrollY > 150 && !nav.classList.contains(theme)) {
        nav.classList.add(theme);
      }
    }
  },
  created () {
    window.addEventListener('scroll', this.handleScroll);
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll);
  },
};
</script>

<style scoped>
.navbar-light .navbar-nav .nav-link{
  color: rgba(0, 0, 0, 1) !important;
}
.navbar-dark .navbar-nav .nav-link{
  color: rgba(255, 255, 255, 1) !important;
}

.title-text {
  font-family: "Roboto", sans-serif;
  font-size: 0.9rem !important;
}

@media(min-width: 991px){
  .nav-item-padding-first {
    padding-left: 0 !important;
  }

  .nav-item-padding {
    padding-left: 0 !important;
  }
}

.nav-item-padding-first {
  padding-left: 4vw !important;
}

.nav-item-padding {
  padding-left: 32px !important;
}

#myNavBar {
  transition: ease-in-out .3s;
}

/* Style 3: Just animate the :after element's width from 0 to 100% */

.menu a:after {
  content: "";
  position: absolute;
  width: 0%;
  height: 1px;
  display: block;
  transition: all 0.3s ease;
  bottom: 25%;
}

.menu a:hover::after {
  width: 1.5%;
  height: 1px;
  background-color: #fff; /** Need to change this based on the selected theme */
}

.container-padding-custom {
  padding: 0 !important;
}
</style>
